import domain from '../../api/domain.js'
export default {
    props: {
        imageCode: {
            type: String,
            default: ''
        },
        refreshAgain: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            imgSrc: ''
        }
    },
    watch: {
        refreshAgain() {
            this.refresh();
        }
    },
    methods: {
        refresh() {
            this.Request('getVRCodeImg', {
                type: 'forget',
                width: 135,
                height: 32,
                timestamp: new Date().getTime()
            }).then(res => {
                this.imgSrc = res.tempFilePath
            })
        }
    },
    beforeMount() {
        this.refresh();
    },
}
